<template>
  <!-- <div> -->
    <!-- <el-input class="mg-bt" v-model="fe_filter" v-if="filter" placeholder=""></el-input> -->
    <el-table :data="formatData" :row-style="showRow" v-bind="$attrs" ref="tree-table"
     :border="border" :height="height" header-row-class-name="header-row-class" highlight-current-row
     @selection-change="handleSelectionChange" @select="tableSelect ">
      <el-table-column width="55" v-if='checkBox' type="selection" reserve-selection></el-table-column>
      <el-table-column v-if="columns.length===0" width="150">
        <template slot-scope="scope">
          <span v-for="space in scope.row._level" class="ms-tree-space" :key="space"></span>
          <span class="tree-ctrl" v-if="iconShow(0,scope.row)" @click="toggleExpanded(scope.$index)">
            <i v-if="!scope.row._expanded" class="el-icon-plus"></i>
            <i v-else class="el-icon-minus"></i>
          </span>
          {{scope.$index}}
        </template>
      </el-table-column>
      <el-table-column v-else v-for="(column, index) in columns" :key="column.value" :label="column.text" :width="column.width">
        <template slot-scope="scope">
          <template v-if="index === 0">
            <span v-for="space in scope.row._level" class="ms-tree-space" :key="space"></span>
          </template>
          <span class="tree-ctrl" v-if="iconShow(index,scope.row)" @click="toggleExpanded(scope.$index)">
            <i v-if="!scope.row._expanded" class="el-icon-plus"></i>
            <i v-else class="el-icon-minus"></i>
          </span>
          {{scope.row[column.value]}}
        </template>
      </el-table-column>
      <slot></slot>
    </el-table>
  <!-- </div> -->
</template>

<script>
/**
 * Auth: Lei.j1ang
 * Created: 2018/1/19-13:59
 * guthub: https://github.com/PanJiaChen/vue-element-admin/tree/master/src/components/TreeTable
 * readme
 * edit: weilan
 * github: https://github.com/hql7
 * vue-element-adminTreeTable
 * 
 */
import treeToArray from "./eval";
import { flattenDeep, flattenDeepParents, regDeepParents } from './array';

export default {
  name: "treeTable",
  data() {
    return {
      multipleSelection: [], // 
      fe_filter: "", // 
      row_id: "", // id
      otableData: [], //  
      filterData: [] // 
    };
  },
  props: {
    // 
    data: {
      type: [Array, Object],
      required: true
    },
    // 
    columns: {
      type: Array,
      default: () => []
    },
    evalFunc: Function,
    evalArgs: Array,
    expandAll: {
      type: Boolean,
      default: false
    },
    // nodekey,children
    options: {
      type: Object,
      default: () => {
        return {
          key: "id",
          children: "children"
        }
      }
    },
    // 
    checkBox: {
      type: Boolean,
      default: false
    },
    //  
    height: Number,
    // 
    border: {
      type: Boolean,
      default: true
    },
    // 
    disabled: String,
    /* // 
    filter: {
      type: Boolean,
      default: false
    },
    // 
    filterOptions: {
      type: Object,
      default: () => {
        return {
          label: "Name",
          children: "Children"
        };
      }
    }, */
    // 
    parentChild: {
      type: Boolean,
      default: false
    } 
  },
  computed: {
    // 
    formatData: function() {
      let tmp;
      if (!Array.isArray(this.data)) {
        tmp = [this.data];
      } else {
        tmp = this.data;
      }
      const func = this.evalFunc || treeToArray;
      const args = this.evalArgs
        ? Array.concat([tmp, this.expandAll], this.evalArgs)
        : [tmp, this.expandAll];
      return func.apply(null, args);
    },
  },
  /* watch: {
    // 
    fe_filter(val) {
      if (val) {
        this.filterData = [];
        this.tableFilter(this.otableData, val);
      }
    }
  }, */
  methods: {
    showRow: function(row) {
      const show = row.row.parent
        ? row.row.parent._expanded && row.row.parent._show
        : true;
      row.row._show = show;
      return show
        ? "animation:treeTableShow 1s;-webkit-animation:treeTableShow 1s;"
        : "display:none;";
    },
    // 
    toggleExpanded(trIndex, row) {
      const record = this.formatData[trIndex];
      record._expanded = !record._expanded;
      this.$nextTick(()=>{
        this.$refs["tree-table"].doLayout();
      })
      this.$emit("openChildren", row);
    },
    // 
    iconShow(index, record) {
      return index === 0 && record[this.options.children] && record[this.options.children].length > 0;
    },
    // 
    currentDicItemDtoChange(row) {
      if (this.row_id === row[this.options.key]) {
        this.$refs["tree-table"].setCurrentRow();
        this.row_id = "";
        this.$emit("treeTableClick", {});
      } else {
        this.row_id = row[this.options.key];
        this.$emit("treeTableClick", row);
      }
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;      
      this.$emit("treeTableSelect", val);
    },
    // 
    tableSelect(val, row) {
      if(!this.parentChild) return;
      
      // 
      if(val.some(item => item[this.options.key] == row[this.options.key])){
        // 
        for(let item of val){
          if(!item[this.options.children] || item[this.options.children].length == 0) continue;
          item.Children.forEach(i => {
            this.$refs["tree-table"].toggleRowSelection(i, true);
          })
        }
        // 
        regDeepParents(row,"parent", parents => {
         let reg = parents && parents[this.options.children].every(item => {
            return this.multipleSelection.some(it => it[this.options.key] == item[this.options.key]);
          });
         if(reg) this.$refs["tree-table"].toggleRowSelection(parents, true);
        });
      }else{
        // 
        let cancel_data = [...flattenDeep(row[this.options.children], this.options.children), ...flattenDeepParents([row], "parent")];
        for(let item of cancel_data){
          this.$refs["tree-table"].toggleRowSelection(item, false);
        }
      }
    },
    // 
    tableFilter(data, val) {
      data.forEach(item => {
        if (~item[this.filterOptions.label].indexOf(val)) {
          this.filterData.push(item);
        } else {
          if (
            item[this.filterOptions.children] &&
            item[this.filterOptions.children].length > 0
          ) {
            this.tableFilter(item[this.filterOptions.children], val);
          }
        }
      });
    },
    // 
    useDisabled(row){
      return this.disabled ? row[this.disabled] : true;
    }
  }
};
</script>
<style rel="stylesheet/css">
@keyframes treeTableShow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes treeTableShow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>

<style lang="scss" rel="stylesheet/scss" scoped>
$color-blue: #2196f3;
$space-width: 18px;
.ms-tree-space {
  position: relative;
  top: 1px;
  display: inline-block;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  width: $space-width;
  height: 14px;
  &::before {
    content: "";
  }
}
.processContainer {
  width: 100%;
  height: 100%;
}
table td {
  line-height: 26px;
}

.tree-ctrl {
  position: relative;
  cursor: pointer;
  color: $color-blue;
  margin-left: -$space-width;
}

.i-icon {
  margin-right: 6px;
}
</style>
